					margin:0 auto;
					width:660px;
					table{
						width:100%;
						font-size:12px;
						margin-top: 20px;
						margin-bottom:30px;
						table th{
							background:#f5f6fb;
							color:#8a93ba;
						}
					}
          .money{
						font-size: 16px;
						margin-bottom: 20px;
						span{
							color:#ec8073;
						}
					}
					.inf{
						font-size: 16px;
						margin-bottom:20px;
						input{
							width:575px;
							height:30px;
							border: 1px #e8e8f4 solid;
							background:#f2f5fe;
						}
					}
				}
				.invoice-btn{
					margin:0 auto;
					width:660px;
					height: 50px;
					.btn-r{
						float: right;
						line-height: 50px;
						input{
							width:90px;
							height:30px;
							border-radius:3px;
						}
						input:nth-of-type(1){
							background:#0c9b0b;
							color:white;
						}
						input:nth-of-type(2){
							color:#0c9b0b;
							border:#0c9b0b 1px solid;
						}
					}
				}
			}
		}
</style>



